import "./Navi.css";
import { motion } from "../../node_modules/framer-motion/dist/framer-motion";

export default function Navi() {
  return (
    <motion.div>
      <div
        className="homeRaid"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
      >
        <p>Navi</p>
      </div>
      <div className="footerRaid">
        <nav>
          <ul>
            <li>Last Wish</li>
            <li>Garden of Salvation</li>
            <li>Vault of Glass</li>
            <li>Deep Stone Crypte</li>
          </ul>
        </nav>
      </div>
    </motion.div>
  );
}
